.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 16;
  width: 100%;
  height: 100%;
  background-color: #000000;
}

.modal__show {
  display: block;
  animation-name: glide;
  animation-duration: 1s;
}

.modal__video {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 60px;
  height: 60px;
  padding: 0;
  background-color: rgba(0, 0, 0, 0.7);
  border: none;
  border: 2px solid #00ccff;
  border-radius: 50%;
  animation: pulse 1.5s infinite alternate;
  transition: 0.2s;
}

.modal__close::before, .modal__close::after {
  content: '';
  position: absolute;
  left: 50%;
  margin-left: -15px;
  width: 30px;
  height: 2px;
  border-radius: 50%;
  background-color: #ffffff;
  transition: 0.2s;
}

.modal__close::before {
  transform: rotate(-45deg);
}

.modal__close::after {
  transform: rotate(45deg);
}

.modal__close:active {
  box-shadow: 0 0 3px 1px #00ccff;
  animation: none;
}

.modal__close:active::before, .modal__close:active::after {
  background-color: #00ccff;
}
